import { buziAPI } from '@/api';
import mixins from '@/mixins/myMixins';
export default {
	data() {
		return {
			keyWord:'',
			serachObj: {} //数据
		};
	},
	components: {},
	watch: {},
	mixins: [mixins],
	onLoad() {
		let that = this;
		buziAPI.getSerachIndex(res => {
			that.serachObj = res.data;
		});
	},
	//上拉加载更多
	onReachBottom() {},
	methods: {
		serach(val){
			console.log(val)
			uni.navigateTo({
				url:'../webView/webView?name=' + val
			})
		}
	}
};
